<template>
  <div class="com-container">
      <!-- 热卖组件 -->
      <div class="com-chart" ref="stock_ref"> 
          
      </div>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import { getThemeValue } from '@/utils/theme_utils'
export default {
    data () {
            return {
                chartInstance:null,
                allData:null,//服务器返回的数据
                currentIndex:0,//当前所展示出的分类数据页数
                // titleFontSize:0
                timerId:null ,//定时器标识
            }
    },
    created () {
      //组件创建完成进行回调函数的注册
      this.$socket.registerCallBack('stockData',this.getData)
    },
    computed :{
      ...mapState(['theme'])
    },
    mounted(){
      this.initChart()
      //this.getData()
      this.$socket.send({
            action: 'getData',
            socketType: 'stockData',
            chartName: 'stock',
            value: ""
        })
      window.addEventListener('resize',this.screenAdapter)
      this.screenAdapter()//主动进行第一次适配
    },
    destroyed () {
     window. removeEventListener('resize',this.screenAdapter)
     clearInterval(this.timerId)
     this.$socket.unregisterCallBack("stockData")
    },  
    methods:{
      async initChart(){
        this.chartInstance= this.$echarts.init(this.$refs.stock_ref,this.theme)
        //地图数据并不在koa2中，不能使用this.$http
        // const ret = await axios.get('http://localhost:8080/static/map/china.json')
        // //console.log(ret)
        // this.$echarts.registerMap('china',ret.data)
        const initOption = {
            title:{
                text:'|库存和销量分析',
                left:20,
                top:20
            }
        }  
        this.chartInstance.setOption(initOption)
        this.chartInstance.on('mouseover',() =>{
            clearInterval(this.timerId)
        })
        this.chartInstance.on('mouseout',() =>{
            this.startInterval()
        })
      },
      getData(ret) {
            //获得销售数据
            //const {data:ret}=await this.$http.get('stock')
            console.log(ret)
            this.allData=ret
        //   this.allData.sort((a,b)=>{
        //       return a.value-b.value  //从小到大的排序
        //   })
            this.updateChart()
            this.startInterval()
        },
        updateChart(){
            const centerArr =[
                ['18%','40%'],
                ['50%','40%'],
                ['82%','40%'],
                ['34%','75%'],
                ['66%','75%']
            ]
            const colorArr =[
                ['#4FF778','#0BA82C'],
                ['#E5DD45','#E8B11C'],
                ['#E8821C','#E55455'],
                ['#5052EE','#AB6EE5'],
                ['#23E5E5','#2E72BF']
            ]
            const start =this.currentIndex*5
            const end=(this.currentIndex+1)*5
            const showData =this.allData.slice(start,end)
            //返回一个数组
            const seriesArr =showData.map((item,index) =>{
                return {
                    type:'pie',
                    // radius:[110,100],
                    
                    center:centerArr[index],
                    hoverAnimation:false,//关闭鼠标移入饼图时的动画效果
                    labelLine:{
                        show:false//指示线的移除
                    },
                    label:{
                        position:'center',
                        color:colorArr[index][0]
                    },
                    data:[
                        {
                            name:item.name+'\n\n'+item.sales,
                            value:item.sales,
                            itemStyle:{
                                color:new this.$echarts.graphic.LinearGradient(0,1,0,0,[
                              {
                                  offset:0,
                                  color:colorArr[index][0]
                              },
                              {
                                  offset:1,
                                  color:colorArr[index][1]
                              }
                          ])
                            }
                        },
                        {
                            value:item.stock,
                            itemStyle:{
                                color:'#333843'
                            }
                        }
                    ]
                }
            })
            const dataOption = {
                series:seriesArr
            }
            this.chartInstance.setOption(dataOption)
        },
        screenAdapter () {
            const titleFontSize =this.$refs.stock_ref.offsetWidth /100 *3.6
            const innerRadius= titleFontSize*2.8
            const outterRadius=innerRadius*1.125
            const adapterOption = {
                title:{
                  textStyle:{
                      fontSize:titleFontSize
                  }
                },
                series:[
                    {
                        type:'pie',
                        radius:[outterRadius,innerRadius],
                        label:{
                            fontSize:titleFontSize/2
                        }
                    },
                    {   type:'pie',
                        radius:[outterRadius,innerRadius],
                        label:{
                            fontSize:titleFontSize/2
                        }
                    },
                    {   type:'pie',
                        radius:[outterRadius,innerRadius],
                        label:{
                            fontSize:titleFontSize/2
                        }
                    },
                    {   type:'pie',
                        radius:[outterRadius,innerRadius],
                        label:{
                            fontSize:titleFontSize/2
                        }
                    },
                    {   type:'pie',
                        radius:[outterRadius,innerRadius],
                        label:{
                            fontSize:titleFontSize/2
                        }
                    },
                    
                ]
            }
            this.chartInstance.setOption(adapterOption)
            this.chartInstance.resize()
        },
        startInterval () {
            if(this.timerId){
                clearInterval(this.timerId)
            }
            this.timerId=setInterval(() =>{
                this.currentIndex++
                if(this.currentIndex>1){
                    this.currentIndex=0
                }
                this.updateChart()
            },5000)
            
        }
    },
    
  watch :{
      theme(){
          this.chartInstance.dispose()
          this.initChart()
          this.screenAdapter()
          this.updateChart()
      }
  }
}
</script>

